"use client"

import { useState } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { KpiStatusBadge, ReviewStatusBadge, RiskBadge, CriticalityBadge } from "@/components/status-badge"
import { CheckCircle2, AlertTriangle, XCircle, Clock, FileSearch, TrendingUp, Filter } from "lucide-react"
import { controls, kpiDefinitions, kpiExecutions, actionPlans, users, getKpiById, getControlById, getUserById, getPendingReviews, getOverdueActionPlans } from "@/lib/mock-data"
import Link from "next/link"
import { DashboardChart } from "./dashboard-chart"

export function DashboardContent() {
  const [period, setPeriod] = useState("2025-12")
  const [framework, setFramework] = useState("SOX")

  // Calculate stats for the period
  const periodExecutions = kpiExecutions.filter(
    (e) => e.period === period && e.version === Math.max(...kpiExecutions.filter((x) => x.kpiId === e.kpiId && x.period === period).map((x) => x.version))
  )

  const greenCount = periodExecutions.filter((e) => e.status === "Green").length
  const yellowCount = periodExecutions.filter((e) => e.status === "Yellow").length
  const redCount = periodExecutions.filter((e) => e.status === "Red").length
  const total = greenCount + yellowCount + redCount

  const greenPct = total > 0 ? Math.round((greenCount / total) * 100) : 0
  const yellowPct = total > 0 ? Math.round((yellowCount / total) * 100) : 0
  const redPct = total > 0 ? Math.round((redCount / total) * 100) : 0

  // KPIs not executed
  const monthlyKpis = kpiDefinitions.filter((k) => k.frequency === "Mensal")
  const executedKpiIds = new Set(periodExecutions.map((e) => e.kpiId))
  const missingKpis = monthlyKpis.filter((k) => !executedKpiIds.has(k.id))

  const pendingReviews = getPendingReviews()
  const overdueActions = getOverdueActionPlans()

  // Attention items (Red/Yellow)
  const attentionItems = periodExecutions
    .filter((e) => e.status === "Red" || e.status === "Yellow")
    .map((e) => {
      const kpi = getKpiById(e.kpiId)
      const control = getControlById(e.controlId)
      return { ...e, kpi, control }
    })

  return (
    <div className="flex flex-col gap-6">
      {/* Header */}
      <div className="flex flex-col gap-4 sm:flex-row sm:items-center sm:justify-between">
        <div>
          <h1 className="text-2xl font-bold text-foreground">Dashboard</h1>
          <p className="text-sm text-muted-foreground">Visao geral dos KPIs de compliance</p>
        </div>
        <div className="flex flex-wrap items-center gap-3">
          <Select value={framework} onValueChange={setFramework}>
            <SelectTrigger className="w-32 h-9">
              <SelectValue />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="SOX">SOX</SelectItem>
              <SelectItem value="SOC">SOC</SelectItem>
              <SelectItem value="ISO27001">ISO 27001</SelectItem>
              <SelectItem value="PCI">PCI</SelectItem>
            </SelectContent>
          </Select>
          <Select value={period} onValueChange={setPeriod}>
            <SelectTrigger className="w-36 h-9">
              <SelectValue />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="2025-12">Dez 2025</SelectItem>
              <SelectItem value="2025-11">Nov 2025</SelectItem>
              <SelectItem value="2025-10">Out 2025</SelectItem>
              <SelectItem value="2025-09">Set 2025</SelectItem>
              <SelectItem value="2025-08">Ago 2025</SelectItem>
              <SelectItem value="2025-07">Jul 2025</SelectItem>
            </SelectContent>
          </Select>
          <Button variant="outline" size="sm" className="h-9 gap-2 bg-transparent">
            <Filter className="h-4 w-4" />
            Mais Filtros
          </Button>
        </div>
      </div>

      {/* Summary Cards */}
      <div className="grid gap-4 sm:grid-cols-2 lg:grid-cols-4">
        <Card className="border border-emerald-200 bg-emerald-50/50">
          <CardContent className="p-5">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-emerald-600">Green</p>
                <p className="text-3xl font-bold text-emerald-700">{greenPct}%</p>
                <p className="text-xs text-emerald-600/70">{greenCount} de {total} KPIs</p>
              </div>
              <div className="flex h-12 w-12 items-center justify-center rounded-full bg-emerald-100">
                <CheckCircle2 className="h-6 w-6 text-emerald-600" />
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="border border-amber-200 bg-amber-50/50">
          <CardContent className="p-5">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-amber-600">Yellow</p>
                <p className="text-3xl font-bold text-amber-700">{yellowPct}%</p>
                <p className="text-xs text-amber-600/70">{yellowCount} de {total} KPIs</p>
              </div>
              <div className="flex h-12 w-12 items-center justify-center rounded-full bg-amber-100">
                <AlertTriangle className="h-6 w-6 text-amber-600" />
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="border border-red-200 bg-red-50/50">
          <CardContent className="p-5">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-red-600">Red</p>
                <p className="text-3xl font-bold text-red-700">{redPct}%</p>
                <p className="text-xs text-red-600/70">{redCount} de {total} KPIs</p>
              </div>
              <div className="flex h-12 w-12 items-center justify-center rounded-full bg-red-100">
                <XCircle className="h-6 w-6 text-red-600" />
              </div>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="p-5">
            <div className="flex flex-col gap-3">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm font-medium text-muted-foreground">Pendencias</p>
                </div>
                <Clock className="h-5 w-5 text-muted-foreground" />
              </div>
              <div className="flex flex-col gap-1.5">
                <div className="flex items-center justify-between text-sm">
                  <span className="text-muted-foreground">KPIs nao lancados</span>
                  <span className="font-semibold text-foreground">{missingKpis.length}</span>
                </div>
                <div className="flex items-center justify-between text-sm">
                  <span className="text-muted-foreground">Revisoes pendentes</span>
                  <span className="font-semibold text-primary">{pendingReviews.length}</span>
                </div>
                <div className="flex items-center justify-between text-sm">
                  <span className="text-muted-foreground">Planos atrasados</span>
                  <span className="font-semibold text-red-600">{overdueActions.length}</span>
                </div>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Chart */}
      <Card>
        <CardHeader className="pb-2">
          <div className="flex items-center justify-between">
            <CardTitle className="text-base font-semibold">Tendencia - Ultimos 6 Periodos</CardTitle>
            <TrendingUp className="h-4 w-4 text-muted-foreground" />
          </div>
        </CardHeader>
        <CardContent>
          <DashboardChart />
        </CardContent>
      </Card>

      {/* Attention Table */}
      <Card>
        <CardHeader className="pb-2">
          <div className="flex items-center justify-between">
            <CardTitle className="text-base font-semibold">Atencao Imediata</CardTitle>
            <Badge variant="secondary" className="text-xs">
              {attentionItems.length} itens
            </Badge>
          </div>
        </CardHeader>
        <CardContent>
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Controle</TableHead>
                <TableHead>KPI</TableHead>
                <TableHead>Status</TableHead>
                <TableHead>Criticidade</TableHead>
                <TableHead>Responsavel</TableHead>
                <TableHead>Valor</TableHead>
                <TableHead className="text-right">Acao</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {attentionItems.length === 0 ? (
                <TableRow>
                  <TableCell colSpan={7} className="text-center py-8 text-muted-foreground">
                    Nenhum KPI em atencao para o periodo selecionado
                  </TableCell>
                </TableRow>
              ) : (
                attentionItems.map((item) => (
                  <TableRow key={item.id}>
                    <TableCell>
                      <div className="flex flex-col">
                        <span className="text-xs text-muted-foreground">{item.control?.code}</span>
                        <span className="text-sm font-medium text-foreground">{item.control?.name}</span>
                      </div>
                    </TableCell>
                    <TableCell className="text-sm text-foreground">{item.kpi?.name}</TableCell>
                    <TableCell>
                      <KpiStatusBadge status={item.status} />
                    </TableCell>
                    <TableCell>
                      {item.kpi && <CriticalityBadge criticality={item.kpi.criticality} />}
                    </TableCell>
                    <TableCell className="text-sm text-muted-foreground">
                      {item.control?.pontoFocal.name}
                    </TableCell>
                    <TableCell className="text-sm font-medium text-foreground">
                      {String(item.value)}
                      {item.kpi?.type === "numeric" ? "%" : ""}
                    </TableCell>
                    <TableCell className="text-right">
                      <Button variant="ghost" size="sm" asChild>
                        <Link href={`/controles/${item.controlId}`}>
                          Ver detalhe
                        </Link>
                      </Button>
                    </TableCell>
                  </TableRow>
                ))
              )}
            </TableBody>
          </Table>
        </CardContent>
      </Card>
    </div>
  )
}
