// Helper functions
export function getControlById(id: string) {
  return controls.find((c) => c.id === id)
}

export function getKpisByControlId(controlId: string) {
  return kpiDefinitions.filter((k) => k.controlId === controlId)
}

export function getKpiById(id: string) {
  return kpiDefinitions.find((k) => k.id === id)
}

export function getUserById(id: string) {
  return users.find((u) => u.id === id)
}

export function getLatestExecution(kpiId: string, period?: string) {
  const execs = kpiExecutions
    .filter((e) => e.kpiId === kpiId && (!period || e.period === period))
    .sort((a, b) => b.version - a.version)
  return execs[0] || null
}

export function getExecutionsByKpiId(kpiId: string) {
  return kpiExecutions.filter((e) => e.kpiId === kpiId)
}

export function getActionPlansByExecutionId(executionId: string) {
  return actionPlans.filter((ap) => ap.executionId === executionId)
}

export function getActionPlansByControlId(controlId: string) {
  return actionPlans.filter((ap) => ap.controlId === controlId)
}

export function getReviewByExecutionId(executionId: string) {
  return securityReviews.filter((r) => r.executionId === executionId)
}

export function getPendingReviews() {
  const reviewedIds = new Set(securityReviews.map((r) => r.executionId))
  return kpiExecutions.filter((e) => !e.isDraft && !reviewedIds.has(e.id))
}

export function getOverdueActionPlans() {
  const today = new Date().toISOString().split("T")[0]
  return actionPlans.filter((ap) => ap.status !== "Concluido" && ap.dueDate < today)
}
