import { cn } from "@/lib/utils"
import type { KpiStatus, ReviewStatus, ActionPlanStatus, RiskLevel, Criticality } from "@/lib/types"
import { CheckCircle2, AlertTriangle, XCircle, Clock, MinusCircle } from "lucide-react"

export function KpiStatusBadge({ status, size = "default" }: { status: KpiStatus; size?: "sm" | "default" }) {
  const config = {
    Green: { label: "Green", className: "bg-emerald-50 text-emerald-700 border-emerald-200", icon: CheckCircle2 },
    Yellow: { label: "Yellow", className: "bg-amber-50 text-amber-700 border-amber-200", icon: AlertTriangle },
    Red: { label: "Red", className: "bg-red-50 text-red-700 border-red-200", icon: XCircle },
  }

  const c = config[status]
  const Icon = c.icon

  return (
    <span
      className={cn(
        "inline-flex items-center gap-1 rounded-full border font-semibold",
        c.className,
        size === "sm" ? "px-2 py-0.5 text-[10px]" : "px-2.5 py-0.5 text-xs"
      )}
    >
      <Icon className={cn(size === "sm" ? "h-3 w-3" : "h-3.5 w-3.5")} />
      {c.label}
    </span>
  )
}

export function ReviewStatusBadge({ status }: { status: ReviewStatus }) {
  const config = {
    Approved: { label: "Aprovado", className: "bg-emerald-50 text-emerald-700 border-emerald-200", icon: CheckCircle2 },
    NeedsAdjustments: { label: "Ajuste Solicitado", className: "bg-amber-50 text-amber-700 border-amber-200", icon: AlertTriangle },
    Rejected: { label: "Reprovado", className: "bg-red-50 text-red-700 border-red-200", icon: XCircle },
    Pending: { label: "Pendente", className: "bg-slate-50 text-slate-600 border-slate-200", icon: Clock },
  }

  const c = config[status]
  const Icon = c.icon

  return (
    <span className={cn("inline-flex items-center gap-1 rounded-full border px-2.5 py-0.5 text-xs font-semibold", c.className)}>
      <Icon className="h-3.5 w-3.5" />
      {c.label}
    </span>
  )
}

